// global layout of all pages
import React, { Fragment } from 'react';
import styled from '@emotion/styled';
import { Helmet } from 'react-helmet';

import Header from '../components/Header';
import Footer from '../components/Footer';
import RangaFont from '../../static/fonts/ranga.woff2';
import RobotoFont from '../../static/fonts/roboto.woff2';
import GlobalStyles from '../global';
import '../styles/fonts.scss';

// ssr workaround
if (typeof window === 'undefined') {
  global.window = {};
  global.document = {};
}

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
`;

const Content = styled.main`
  flex: 1;
  margin: calc(3em + 3vh) 0px;
  padding-bottom: 3rem;
  position: relative;
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr minmax(8em, 40em) 1fr 1fr 1fr;
  grid-auto-flow: dense;
  grid-auto-rows: max-content;
  gap: 0px 4vw;
`;

export default function Layout(props) {
  const { children, activeTag } = props;
  // need outer container: https://stackoverflow.com/questions/1709442/make-divs-height-expand-with-its-content
  // https://dev.to/hzburki/100-height-to-all-divs-gatsby-33nd
  // https://github.com/gatsbyjs/gatsby/issues/7310
  return (
    <>
      <>
        <Helmet>
          {/* preload font to accelerate loading
         https://github.com/gatsbyjs/gatsby/issues/13074
          */}
          <link
            rel="preload"
            as="font"
            href={RangaFont}
            type="font/woff2"
            crossOrigin="anonymous"
          />
          <link
            rel="preload"
            as="font"
            href={RobotoFont}
            type="font/woff2"
            crossOrigin="anonymous"
          />
        </Helmet>
      </>
      <GlobalStyles />
      <Wrapper>
        <Header activeTag={activeTag} />
        <Content>{children}</Content>
        <Footer />
      </Wrapper>
    </>
  );
}
